import { Component, ChangeDetectionStrategy, inject, DestroyRef } from '@angular/core';
import { FormGroup, Validators, FormsModule, ReactiveFormsModule, FormControl, NonNullableFormBuilder } from '@angular/forms';
import { Router, RouterLink } from '@angular/router';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzCheckboxModule } from 'ng-zorro-antd/checkbox';
import { NzWaveModule } from 'ng-zorro-antd/core/wave';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NzGridModule } from 'ng-zorro-antd/grid';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { NzInputModule } from 'ng-zorro-antd/input';
import { NzNotificationModule } from 'ng-zorro-antd/notification';
import { NzTabsModule } from 'ng-zorro-antd/tabs';
import { IcomLibsProjectLoginService } from '@icom/services/projects'
import { cloneDeep } from 'lodash';
import { sha256 } from 'js-sha256';
import { LocalStorageService } from '@icom/services/authenticate'
import { IComMessageService } from '@icom/services/common';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { HttpErrorResponse } from '@angular/common/http';
import { MenuDefine, ModuleFunction } from './defines/login.define';
import { IIcomLibsCoreModuleFunction, IIcomLibsCoreAction } from './interfaces/login.interface';
import { DefineKey } from '@icom/defines';
import { IcomLibsCoreEnvironmentPipe } from '@icom/pipes';
import { environment } from "../../../../environments/environment"
 
@Component({   
  selector: 'icom-libs-components-login', 
  templateUrl: './login.component.html', 
  styleUrls: ['./login.component.scss'],
  standalone: true,
  changeDetection: ChangeDetectionStrategy.Default,
  imports: [FormsModule, NzFormModule,
    IcomLibsCoreEnvironmentPipe, ReactiveFormsModule, NzTabsModule, NzGridModule, NzButtonModule, NzInputModule, NzWaveModule, NzCheckboxModule, NzIconModule, RouterLink, NzNotificationModule]
})
export class IcomLibsComponentsLoginComponent {
  protected validateForm: FormGroup<{
    userName: FormControl<string>;
    password: FormControl<string>;
    remember: FormControl<boolean>;
  }> = this.fb.group({
    userName: ['', [Validators.required]],
    password: ['', [Validators.required]],
    remember: [true]
  });
  protected isLoading!: boolean;

  protected IcomLibsProjectLoginService = inject(IcomLibsProjectLoginService);
  protected localStorageService = inject(LocalStorageService);
  protected iComMessageService = inject(IComMessageService);
  protected destroyRef = inject(DestroyRef);
  protected router = inject(Router);

  constructor(private fb: NonNullableFormBuilder) { }


  submitForm(): void {
    if (!this.validateForm.valid) {
      Object.values(this.validateForm.controls).forEach((control: FormControl) => {
        if (control?.invalid) {
          control?.markAsDirty();
          control?.updateValueAndValidity({ onlySelf: true });
        }
      });
      return;
    }

    this.isLoading = true;
    const loginDataModel = {
      username: '',
      password: '',
      workspaceCode: environment.workSpaceCode
    };

    loginDataModel.username = this.validateForm.value.userName?.replace(/\s/g, '').toLowerCase() || '';
    loginDataModel.password = this.validateForm.value?.password?.replace(/\s/g, '') || '';
    const params = {
      user: "corepoint",
      password: '123123'
    }
    this.IcomLibsProjectLoginService.loginMerchant(params as any).subscribe((result: any) => {
      if (result?.data?.access_token) {
        this.localStorageService.setItem('merchantToken', result?.data?.access_token);
      }
    })
    const dataRequest = cloneDeep(loginDataModel);
    dataRequest.password = sha256(dataRequest.password);

    this.IcomLibsProjectLoginService.login(dataRequest).pipe(takeUntilDestroyed(this.destroyRef)).subscribe(
      {
        next: async (response: any) => {
          if (response.code !== 200) {
            return;
          }
          this.processPermission();
          this.localStorageService.setItem('menu', this.getMenuData());
          this.iComMessageService.showAlertMessageDone('Login success');
          this.localStorageService.setItem('token', response?.data?.accessToken);
          this.router.navigateByUrl('/');
          this.isLoading = false;
        },
        error: (err: any) => {
          this.iComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message || 'Login fail');
          this.isLoading = false;
        }

      });
  }

  getMenuData(): any[] {
    const permissions: IIcomLibsCoreModuleFunction[] = this.localStorageService.getItem('permission');
    if (!permissions) {
      return [];
    }

    const menuData = MenuDefine();

    return menuData.map(item => {
      return {
        ...item,
        children: item.children
          ?.filter(child => this.hasSelectedAction(child, permissions))
      };
    }).filter(item => item.children && item.children.length > 0);
  }

  private hasSelectedAction(child: any, permissions: IIcomLibsCoreModuleFunction[]): boolean {
    const permission = permissions
      .find(p => p.sub_modules?.some((group: any) => group.path === child.path));
    console.log(permission)
    if (permission) {
      return permission?.sub_modules?.some((group: any) =>
        group.actions.some((action: any) => action.selected)) || false;
    }

    return false;
  }


  private processPermission() {
    const permissionsAll = ModuleFunction();
    const flattenedPermissions = this.flattenPermissions(permissionsAll);
    this.localStorageService.setItem('permission', permissionsAll);
  }

  private flattenPermissions(permissions: IIcomLibsCoreModuleFunction[]) {
    let flattenedPermissions: IIcomLibsCoreModuleFunction[] = [];

    permissions.forEach(permission => {
      const { name_vi, name_en, parent } = permission;
      permission?.sub_modules?.forEach((group: IIcomLibsCoreModuleFunction) => {
        const { path, actions } = group;
        actions?.length && actions?.forEach((action: IIcomLibsCoreAction) => {
          flattenedPermissions.push({
            ...action,
            name_vi,
            name_en,
            parent,
            path
          });
        });
      });
    });
    // console.log(flattenedPermissions);
    this.localStorageService.setItem(DefineKey.FUNCTIONS, flattenedPermissions);
    return flattenedPermissions;
  }

}
